import React, { useEffect, useState } from 'react'

const Login = () => {
    const [isLogged, setIsLogged] = useState(false)

    const url = 'http://localhost/process/process.php'

    if (isLogged){
        const form = document.querySelector('form')

        form.addEventListener('submit', (e) => {
        e.preventDefault()
    
        const files = document.querySelector('[type=file]').files
        const formData = new FormData()
    
        for (let i = 0; i < files.length; i++) {
            let file = files[i]
    
            formData.append('files[]', file)
        }
        formData.append('prodyctType', document.getElementById('productType').value);
        console.log(formData)
    
        fetch(url, {
            method: 'POST',
            body: formData,
        }).then((response) => {
            console.log(response)
        })
        })
    }
    

    var attempt = 3; // Variable to count number of attempts.
    // Below function Executes on click of login button.
    function validate(){
        var username = document.getElementById("username").value;
        var password = document.getElementById("password").value;
        if ( username === "meno" && password === "heslo"){
            alert ("Prihlasenie prebehlo uspesne");
            setIsLogged(true); 
            return false;
        }
        else{
            attempt --;// Decrementing by one.
            alert("You have left "+attempt+" attempt;");
        // Disabling fields after 3 attempts.
        if( attempt === 0){
            document.getElementById("username").disabled = true;
            document.getElementById("password").disabled = true;
            document.getElementById("submit").disabled = true;
            return false;
        }
        }
    }
    if (isLogged){
        return (
            <>
                <div id='upload'>
                    <form method="post" encType="multipart/form-data">
                        <input type="file" name="files[]" multiple />                        
                        <input type="submit" value="Upload File" name="submit" />                        
                    </form>
                    <select id="productType">
                        <option value="doors">Dvere</option>
                        <option value="kids">Detske izby</option>
                        <option value="kitchen">Kuchyna</option>
                    </select>
                </div>
            </>
        )
    } else {
        return (
            <>
                <div id="login">
                    <form id="form_id" method="post" name="myform">
                        <label>Meno:</label>
                        <input type="text" name="username" id="username"/>
                        <label>Heslo:</label>
                        <input type="password" name="password" id="password"/>
                        <input type="button" value="Login" id="submit" onClick={() => validate()}/>
                    </form>
                </div>
            </>
        )
    }
    
}

export default Login