import React, { useEffect, useState } from 'react'
import { useSelector, useDispatch } from 'react-redux';

const Gallery = () => {

    const [productImages, setProductImages] = useState([])
    const [header, setHeader] = useState ('')
    const type = useSelector (state => state.typeReducer)    

    //Import images
    useEffect(() => {        
        const importAll = (r) => {
            return r.keys().map(r);
        }  
        switch (type) {
            case 'kitchen':
                setProductImages((importAll(require.context('../img/gallery/kitchen', false, /\.(png|jpe?g|svg)$/))));
                setHeader('Kuchyna')
                break;
            case 'doors':
                setProductImages((importAll(require.context('../img/gallery/doors', false, /\.(png|jpe?g|svg)$/))));
                setHeader('Dvere')
                break;  
            case 'inbuilt':
                setProductImages((importAll(require.context('../img/gallery/inbuilt', false, /\.(png|jpe?g|svg)$/))));
                setHeader('Vstavane skrine')
                break;
            case 'bathroom':
                setProductImages((importAll(require.context('../img/gallery/bathroom', false, /\.(png|jpe?g|svg)$/))));
                setHeader('Kupelnovy nabytok')
                break; 
            case 'stairs':
                setProductImages((importAll(require.context('../img/gallery/stairs', false, /\.(png|jpe?g|svg)$/))));
                setHeader('Schodiska')
                break;
            case 'kids':
                setProductImages((importAll(require.context('../img/gallery/kids', false, /\.(png|jpe?g|svg)$/))));
                setHeader('Detske izby')
                break;  
            case 'living':
                setProductImages((importAll(require.context('../img/gallery/living', false, /\.(png|jpe?g|svg)$/))));
                setHeader('Spalne')
                break;
            case 'misc':
                setProductImages((importAll(require.context('../img/gallery/misc', false, /\.(png|jpe?g|svg)$/))));
                setHeader('Ostatne')
                break; 
            default:
                console.log('geno')
        }
        
    },[])   


    const RenderItems = (props) => {
        if (props.images.length > 0) { //Check if images have been loaded
            return (
                props.images.map((x, y) =>                 
                    <div key={`a${y}`} className='galleryInnerDiv'>
                        <img src={props.images[y].default} alt=''></img> 
                    </div> 
                )
            )   
        } else {
            return (
                <>
                </>
            )            
        }
    }    

    return (
        <div>
            <div className="headerFirstDiv">
                <p className="productsHeader">{header}</p>
            </div>
            <div id='galleryOuterDiv'>
                <RenderItems images={productImages} /> 
            </div>
        </div>
        
        )

    }

export default Gallery